.header-area {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 1rem;
  margin-left: 10rem;
  margin-right: 10rem;

}

.menu-bar ul {
  display: flex;
  list-style: none;
  text-transform: uppercase;
  gap: 2rem;
  align-items: center;
  font-size: small;


}

.menu-bar ul li {
  cursor: pointer;
}

.menu-bar ul li:hover {
  color: hsl(0, 94%, 66%);

}

.menu-bar ul li button:hover {
  background-color: white;
  color: hsl(0, 94%, 66%);
}

.menu-bar ul li button {
  background-color: hsl(0, 94%, 66%);
  width: 7rem;
  margin: 0;
  padding: 1rem;
  border-radius: 10px;
  color: white;
  text-transform: uppercase;
  cursor: pointer;
  border: 1px solid hsl(0, 94%, 66%);
}

.top-area {

  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 1rem;
  margin-left: 10rem;
  margin-top: 3rem;

}

.left-area {
  width: 40vw;
  overflow: hidden;
  height: 22rem;
}

.hero-line {
  font-size: 3rem;
  color: hsl(229, 31%, 21%);
}

.hero-para {
  color: hsl(229, 8%, 60%);
  line-height: 1.5rem;
  width: 27rem;
}

.chrome {
  background-color: hsl(231, 69%, 60%);
  color: white;
  width: fit-content;
  padding: 1rem;
  border: none;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  border-radius: 5px;
  margin-right: 2rem;
}

.firefox {
  background-color: #fafafa;
  color: hsl(229, 31%, 21%);
  width: fit-content;
  padding: 1rem;
  border: none;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  margin-right: 2rem;
  border-radius: 5px;



}

.shape {
  position: absolute;
  background-color: hsl(231, 69%, 60%);
  height: 20rem;
  width: 30vw;
  right: 0;
  bottom: 0;
  border-radius: 40% 0% 0% 30%;
  z-index: -10000;
  margin-right: 0;


}

.right-area img {
  margin-right: 1rem;
}

.right-area {
  position: relative;

}

.middle-area {
  display: flex;
  justify-content: center;
  text-align: center;
  flex-direction: column;
  align-items: center;
}

.features h4 {
  color: hsl(229, 31%, 21%);
  font-size: x-large;
}

.features p {
  color: hsl(229, 8%, 60%);
  width: 35vw;
  line-height: 1.5rem;
  margin-bottom: 1rem;
}

.tab-titles {
  margin-top: 2rem;
  margin-bottom: 3rem;
  margin-left: 13rem;
}

.tab-titles ul {
  display: flex;
  gap: 5rem;
  border-bottom: 1px solid hsl(228, 5%, 82%);
  padding-bottom: 2rem;
  width: 45rem;


}

.tab-titles ul li {
  list-style: none;


}

.tab-titles ul li a {
  text-decoration: none;
  color: hsl(229, 8%, 60%);
  font-weight: 500;
  cursor: pointer;

}

.tab-titles ul li a:hover {
  color: hsl(0, 94%, 66%);


}

.tab-titles li a.active {
  color: hsl(229, 31%, 21%);
  border-bottom: 3px solid hsl(0, 94%, 66%);
  padding-bottom: 1.85rem;

}

.tab-title {
  margin-left: -4rem;
}

.tab-area {
  display: flex;
  align-items: center;
  gap: 10rem;


}

.info-area h4 {
  font-size: xx-large;

}

.left {
  display: flex;
  align-items: center;
  position: relative;

}

.info-area {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.info-area p {
  width: 25rem;
  text-align: left;
  color: hsl(229, 8%, 60%);

}

.diagram {
  position: absolute;
  background-color: hsl(231, 69%, 60%);
  height: 20rem;
  width: 50vw;
  left: 0;
  bottom: -3rem;
  border-radius: 0% 40% 20% 0%;
  margin-left: -20rem;
  z-index: -1000;

}

.extension {
  display: flex;
  justify-content: center;
  text-align: center;
  margin-top: 5rem;
  flex-direction: column;
  align-items: center;


}

.cards-container {
  margin-top: 2rem;
  position: relative;
  width: 70vw;
  display: flex;
  justify-content: space-between;
  gap: 3rem;

}

.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  width: 20rem;
  padding: 1rem;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  padding-top: 2rem;
  border-radius: 5px;
}

.card-1 {
  position: absolute;
  left: 0;
}

.card-2 {
  position: absolute;
  left: 22rem;
  top: 2rem;
}

.card-3 {
  left: 44rem;
  position: absolute;
  top: 4rem;


}


.cc {
  margin-left: 3rem;

  border: 2px solid transparent;


}

.cc:hover {
  background-color: white;
  cursor: pointer;
  color: hsl(231, 69%, 60%);
  border: 2px solid hsl(231, 69%, 60%);

}

.shapes {
  width: 20rem;
}

.card-title {
  color: hsl(229, 31%, 21%);
  margin-bottom: -1rem;
  margin-top: 2rem;
}

.faq {
  margin-top: 30rem;
  display: flex;
  justify-content: center;
  text-align: center;
  flex-direction: column;
  align-items: center;
}

.accordion-item {
  width: 40rem;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
}

.accordion-title:hover {
  color: hsl(0, 94%, 66%);
}

.accordion-title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  line-height: 1.5;
  word-spacing: 0.1rem;
  color: hsl(229, 31%, 21%);
  padding: 1rem;
}

.arrow {
  transition: transform 0.3s ease-in-out;
}

.arrow.down {
  transform: rotate(180deg);
}

.active .arrow {
  transform: rotate(180deg);
  fill: hsl(0, 94%, 66%);
}

.accordion-content {
  padding: 1rem;
  margin-top: 1rem;
  line-height: 1.5rem;
  text-align: left;
  margin-bottom: 1rem;
  color: hsl(230, 5%, 55%);

}
.accordion{
  margin-top: 2rem;
}
.cr{
  margin-top: 3rem;
}
.footer{
  height: 50vh;
  margin-top: 5rem;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  background-color: hsl(231, 69%, 60%);
  color: white;
}
.input-container {
  position: relative;
  width: fit-content;
}
form{
  display: flex;
  gap: 1rem;
  position: relative;
}
.heading-1{
  font-size: 1rem;
}
.heading-2{
  font-size: xx-large;
  width: 30rem;
}

input[type="text"]{
  width: 15rem;
  height: 2rem;
  padding: 1rem;
}
.error{
  border: 2px solid hsl(0, 94%, 66%);
}
.error-message{
  position: absolute;
  top: 2rem;
  background-color: hsl(0, 94%, 66%);
  padding: .2rem;
  

}

.error-icon {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
}
input[type="submit"]{
  width: 7rem;
  background-color: hsl(0, 94%, 66%);
  color: white;
  border: 2px solid transparent;
  cursor: pointer;


}
input[type="submit"]:hover{
  background-color: white;
  color: hsl(0, 94%, 66%);
  border: 2px solid hsl(0, 94%, 66%);
}
.blue-area{
  background-color: hsl(229, 31%, 21%);
  color: white;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 1rem;
}
.blue-area .logo{
  background-color: white;
}
.right{
  display: flex;
  gap: 2rem;
  align-items: center;
  
}








